import "./intro.css"

import { useState } from "react";

const Intro = () => {
   const [label, setLabel] = useState("");

   return (
      <div id="Home" className="page intro">
         <div className="intro-title">
            <p>Hello, I'm</p>
            <p>Benjamin Wong</p> 
         </div>
         <div className="intro-links">
            <a
               href="https://chiyeon.itch.io/"
               target="_blank"
               rel="noreferrer"
               onMouseOver={() => setLabel("my games on itch.io")}
               onMouseLeave={() => setLabel("")}
            >
               <img className="intro-link" src="https://i.imgur.com/DRcnLaA.png" alt="itch.io"/>
            </a>
            <a
               href="https://github.com/chiyeon"
               target="_blank"
               rel="noreferrer"
               onMouseOver={() => setLabel("my code on github")}
               onMouseLeave={() => setLabel("")}
            >
            <img className="intro-link" src="https://i.imgur.com/KxrDlSb.png" alt="github.com"/>
            </a>
            <a
               href="https://www.youtube.com/c/chiyeon"
               target="_blank"
               rel="noreferrer"
               onMouseOver={() => setLabel("my videos on youtube")}
               onMouseLeave={() => setLabel("")}
            >
               <img className="intro-link" src="https://i.imgur.com/TbSK055.png" alt="youtube.com"/>
            </a>
         </div>
         <div className="intro-link-label">
            {label}
         </div>
      </div>
   )
}

export default Intro