import "./showcase.css";
import { useState, useEffect } from "react";
import ExpGroup from "../exp-group/exp-group"

const Showcase = (props) => {
   const [selectedItem, setSelectedItem] = useState({});
   const [link, setLink] = useState("");
   /*
   const [title, setTitle] = useState("");
   const [description, setDescription] = useState("");
   const [links, setLinks] = useState([]);
   */

   const SelectItem = (e, item) => {
      setSelectedItem(item);

      // scroll to item
      // inc delay to center properly
      if(!e)
         return;
         // also focus the main page
      props.navtopage(props.id);
      setTimeout(() => {
         

         // center the showcase item
         e.scrollIntoView({
            behavior: "smooth",
            inline: "center",
            block: "nearest"
         });
      }, 250);
   }

   const showcaseItems = props.items.map((item) => {
      return <div
         className={(selectedItem === item) ? "showcase-c-item selected" : "showcase-c-item"}
         key={item.name}
         style={{backgroundImage: `url(${item.image})`}}
         onClick={e => SelectItem(e.target, item)}
      >
         <h3>{item.name}</h3>
      </div>
   });

   useEffect(() => {
      SelectItem(null, props.items[0])
   }, []);

   return (
      <div id={props.id} className="page showcase">
         <div className="showcase-title">
            <h1>{props.title}</h1>
         </div>
         <div className="showcase-carousel-container">
            
            <div className="showcase-carousel">
               {showcaseItems}
            </div>
         </div>
         <div className="showcase-info-container">
            {(typeof selectedItem.links !== "undefined") ?
            <div className="showcase-info page">
               <div className="showcase-info-header">
                  <div className="showcase-info-header-left">
                     <h2>{selectedItem.name}</h2>
                     <p>({selectedItem.date})</p>
                  </div>
                  <div className="showcase-info-header-right">
                     <div className="showcase-links">
                        {selectedItem.links.map((link) => {
                           return <a
                              href={link.link}
                              key={link.link}
                              target="_blank"
                              rel="noreferrer"
                              onMouseOver={() => setLink(`View on ${link.label}`)}
                              onMouseLeave={() => setLink("")}
                           >
                              <img src={link.icon} alt="showcase link" className="showcase-link" />
                           </a>
                        })}
                     </div>
                     <div className="showcase-link-text">
                        {link}
                     </div>
                  </div>
               </div>
               {(typeof selectedItem.awards !== "undefined") ? <div className="showcase-awards">
                  {selectedItem.awards.map(award => {
                     return <div
                        className="showcase-award"
                        key={award.award}
                     >
                        <svg className="showcase-award-icon" style={{fill: award.color}} xmlns="http://www.w3.org/2000/svg" width="17" height="20" viewBox="0 0 24 24" fill="none" stroke="#ffffff" strokeWidth="1.75" strokeLinecap="round" strokeLinejoin="round"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg>
                        {award.award}
                        </div>
                  })}
               </div> : ""}
               <pre>{selectedItem.info.description}</pre>
               <ExpGroup title="Made with" exp={selectedItem.info.tools}/>
            </div>
            : "" }
         </div>
      </div>
   )
}

export default Showcase