import { useState } from "react"

import "./App.css"
import Intro from "./components/intro/intro"
import About from "./components/about/about";
import Showcase from "./components/showcase/showcase"
import NavBar from "./components/navbar/navbar"
import LightSwitch from "./components/light-switch/light-switch";

import { games, apps, videos } from "./data.js"


var pages = ["Home", "About", "Games", "Apps", "Videos"]

const App = () => {
   const [selectedPage, setSelectedPage] = useState("Home")

   const NavToPage = (e) => {
      var page = document.getElementById(e);

      if (!page)
         return;
      
      // TODO dont use id
      // block changes to focus on bottom, or center
      page.scrollIntoView({
         behavior: "smooth",
         block: (e === "About") ? "center" : "start"
      });
   }

   const UpdateSelectedPage = () => {
      var offset = 350;

      pages.forEach(page => {
         var pageElem = document.getElementById(page);

         if (pageElem) {
            var top = pageElem.getBoundingClientRect().top;
            if ((top) >= 0 && (top + offset) <= window.innerHeight)
               setSelectedPage(page);
         }
      })
   }

   return (
      <div className="app"
         onScroll={() => UpdateSelectedPage()}
      >
         <Intro
         />
         <About
         />
         <Showcase
            title="Game Development"
            id="Games"
            items={games}
            navtopage={NavToPage}
         />
         <Showcase
            title="Programming"
            id="Apps"
            items={apps}
            navtopage={NavToPage}
         />
         <Showcase
            title="Video Productions"
            id="Videos"
            items={videos}
            navtopage={NavToPage}
         />
         <NavBar
            pages={pages}
            selected={selectedPage}
            navtopage={NavToPage}
         />
         <LightSwitch />
      </div>
   )
};

export default App;