import { useState } from 'react'
import "./exp-group.css"

const ExpGroup = (props) => {
   const [label, setLabel] = useState("");

   return (
      <div className="exp-group">
         <div className="exp-group-title">
            <h3>{props.title}</h3>
         </div>
         <div className="exp-group-collection-container">
            <div className="exp-group-collection">
               {
               props.exp.map(e => (
                  <img
                     key={e.label}
                     src={e.image}
                     alt={e.label}
                     onMouseEnter={() => setLabel(e.label)}
                     onMouseLeave={() => setLabel("")}
                  ></img>
               ))
               }
            </div>
         </div>
         <div className="exp-group-label"><h3>{label}</h3></div>
      </div>
   )
};

export default ExpGroup;