import "./navbar.css"

const NavBar = (props) => {
   return (
      <div className="navbar">
         {props.pages.map((page) => {
            return <div
               key={page}
               className={(page === props.selected) ? "navbar-element selected" : "navbar-element"}
               onClick={() => props.navtopage(page)}
            >
               {page}
            </div>
         })}
      </div>
   )
}

export default NavBar