import "./light-switch.css";
import { useEffect, useState, useRef } from "react";

const LightSwitch = () => {
   const lightSwitch = useRef(null);
   var darkMode = true;

   useEffect(() => {
      // load our darkmode pref
      var getDarkMode = localStorage.getItem("darkMode");
      if(getDarkMode)
         SetDarkMode(getDarkMode === "true");
   }, [])

   const SetDarkMode = (_darkMode) => {
      darkMode = _darkMode;
      localStorage.setItem("darkMode", darkMode);

      lightSwitch.current.textContent = (darkMode ? "🌙" : "☀️");
      document.documentElement.className = (darkMode ? "darkmode" : "");
   }

   return <div
      className="light-switch"
      onClick={() => SetDarkMode(!darkMode)}
      ref={lightSwitch}
   >
      🌙
   </div>
}

export default LightSwitch;